@import '../variables.scss';

.user-interface {
    display: block;
    width: 600px;
    padding: 20px;
    margin-top: 40px;

    font-size: 20px;
    color: #000;

    background-color: $light-bg;
    border-radius: 12px;

    &__buttons {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 4px;
        border-radius: 12px;
        overflow: hidden;
        // background-color: $accent-dark;
    }

    &__buttons-item {
        display: block;
        padding: 10px;

        font-size: 20px;
        font-weight: 600;

        background-color: $accent-main;
        border: none;

        transition: all .4s;
        cursor: pointer;

        &:hover {
            color: $light-bg;
            background-color: $accent-additional;
        }
    }

    &__input {
        display: flex;
        justify-content: center;
    }

    &__input-item {
        font-size: 20px;
        padding: 10px;
        width: 250px;
        border: solid 1px $accent-additional;
    }

    &__input-btn {
        padding: 10px;

        font-size: 20px;

        background-color: $accent-main;
        border: solid 1px $accent-additional;
        
        transition: all .4s;
        cursor: pointer;

        &:hover {
            background-color: $accent-additional;
            color: #fff;
        }
    }
}