@import '../variables.scss';

.modal-outer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;
    
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 10;
}

.modal {
    display: block;
    width: 800px;
    min-height: 400px;
    padding: 20px;

    font-size: 20px;
    color: #000;

    background-color: $modal-bg;
    border-radius: 12px;
}