@import '../variables.scss';

.img-question {
    display: block;
    width: 600px;
    padding: 20px;
    margin-top: 40px;

    font-size: 20px;
    color: #000;

    background-color: $light-bg;
    border-radius: 12px;

    &__wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        overflow: hidden;
        gap: 4px;
    }

    &__img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__img-wrap {
        position: relative;
        width: 100%;
        height: 100%;

        &:before {
            content: '';
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 10px;
            left: 10px;
            width: 40px;
            height: 40px;
            background-color: #cd0808;
            border-radius: 100%;
            font-size: 30px;
            font-weight: 600;
            color: #fff;
            box-shadow: 0 0 10px #000;
        }

        &:nth-of-type(1):before {
            content: "A";
        }
        &:nth-of-type(2):before {
            content: "B";
        }
        &:nth-of-type(3):before {
            content: "C";
        }
        &:nth-of-type(4):before {
            content: "D";
        }
    }

    &__caption {
        margin-top: 20px;
    }
}