@import '../variables.scss';

.progress {
    display: block;
    width: 600px;
    height: 40px;

    background-color: #035d11;
    border-radius: 12px;

    overflow: hidden;

    &-bar {
        background-color: #27ed44;
        height: 100%;
        border-radius: 12px;
        transition: all 1.5s;
    }
}