@import '../variables.scss';

.multy-question {
    display: block;
    width: 600px;
    padding: 20px;
    margin-top: 40px;

    font-size: 20px;
    color: #000;

    background-color: $light-bg;
    border-radius: 12px;

    &__caption {
        margin-top: 20px;
    }

    &__wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 10px;
        padding-inline-start: 0;
    }

    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 40px;
        background-color: $main-bg;
        transform: rotate3d(10, 0, 0, 180deg);
        transition: transform 2s;
        color: #fff;

        span {
            display: none;

            &.span-active {
                display: block;
            }
        }

        &_showed {
            transform: rotate3d(10, 0, 0, 0deg);
        }
    }
}

.multy-question__item:has(span.span-active) {
    background-color: red;
}